import React from "react";

import "./Work.scss";

export default function Work({work}) {

  const {title, dateStart, dateEnd, description, techs} = work;

  return (
    <div className="Work">
      <span className="date">{dateStart} - {dateEnd}</span>
      <h4>{title}</h4>
      <div className="content">{description.map(d => <p>{d}</p>)}</div>
    </div>
  )
}