import React from 'react';

import './Project.scss';

export default function Project({ title, img, links, children }) {

  return (
    <div className="Project">
      <h4>{title}</h4>

      <div className="content">
        <img src={img} alt="" />
        <p>{children}</p>
      </div>

      <div className="links">
        { links && links.map(({link, text}, i) => <a href={link} target="_blank" key={i}>{text}</a>) }
      </div>
    </div>
  )
}
