import React, { useState } from 'react';

import './Index.scss';

//import Debug from '../../component/Debug/Debug';
import Lang from '../../component/Lang/Lang';
import Section from '../../component/Section/Section';
import Collapse from '../../component/Collapse/Collapse';
import Education from '../../component/Education/Education';
import Project from '../../component/Project/Project';
import Work from '../../component/Work/Work';

import Langs from '../../i18n/lang'; // call axyos /lang.json

import penguin from '../../img/penguin.svg';

export default function Index() {

  const [lang, setLang] = useState('fr');

  return (
    <div id="index">
      {/* <Debug>lang: {lang}</Debug> */}

      <header className="left">
        <div className="picture">
          <img src={penguin} alt="Penguin" />
        </div>
        <h1>Nicolas Tabountchikoff</h1>
        <h2>{Langs[lang].work}</h2>
        <h3><span className="icon-location"></span> Bordeaux, France</h3>
        <div className="icons">
          {/* <a href="https://github.com/taboun-n/" target="_blank" rel="noopener noreferrer">
            <span className="icon-github"></span>
          </a> */}

          <a href="https://gitlab.com/taboun-n" target="_blank" rel="noopener noreferrer">
            <span className="icon-gitlab"></span>
          </a>

          <a href="/resume.pdf" target="_blank" rel="noopener noreferrer">
            <span className="icon-pdf"></span>
          </a>
        </div>

        <Lang lang={lang} langs={['fr', 'en']} setLang={(lang) => setLang(lang) } />
      </header>

      <div className="right">
        <Collapse title={Langs[lang].project.title}>
          { Langs[lang].project.projects.map((p, i) =>
            <Project
              title={p.title}
              img={p.img}
              links={p.links}
              key={i}
            >
              {p.text}
            </Project>
          )}
        </Collapse>

        <Collapse title={Langs[lang].skill.title}>
          { Langs[lang].skill.skills.map((s, i) =>
            <Section title={s.title} key={i}>{s.text.map(t => <p>{t}</p>)}</Section>
          )}
          <div className="techs">
            { Langs[lang].skill.techs.map((t, i) =>
              <img src={`/images/techs/${t}.svg`} alt={t} key={i} />
            )}
          </div>
        </Collapse>

        <Collapse title={Langs[lang].experience.title}>
          { Langs[lang].experience.experiences.map(e => <Work work={e} /> ) }
        </Collapse>

        <Collapse title={Langs[lang].education.title}>
          { Langs[lang].education.schools.map((s,i) =>
            <Education date={s.date} education={s.degree} isGraduated={s.isGraduated} />
          )}
        </Collapse>
        
        <p className="wip">WORK IN PROGRESS • Developed with <span className="icon-heart"></span></p>
      </div>
    </div>
  )
}