import React, {useState} from 'react';

import './Collapse.scss';

export default function Collapse({ title, children }) {
  return (
    <details className="Collapse" open>
      <summary>
        <h3>{title}</h3>
        <i className="icon-"></i>
      </summary>

      <div className="content">{children}</div>
    </details>
  );
}
