import React from "react";

import './Lang.scss';

export default function Lang({lang, setLang, langs}) {

  return (
    <div className="Lang">
      { langs && langs.map((l, i) =>
        <span className={l === lang ? "active" : "" } onClick={() => setLang(l)} key={i}>{l}</span>
      )}
    </div>
  )
}